.applicationInfo {
  box-sizing: border-box;
  width: 100%;
  font-family: Calibri;
}
.applicationInfo .title {
  font-size: 1.1em;
  font-weight: bold;
  padding-bottom: 0.7em;
  color: #373742;
}
.applicationInfo .link {
  color: #2a70c8;
  text-decoration: underline;
}
.applicationInfo .separator {
  display: none;
  border-bottom: 1px solid #e1e1e1;
  margin: 0.75em auto;
}
.applicationInfo .accent {
  font-weight: bold;
}
.applicationInfo .datatable {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}
.applicationInfo .datatable tbody {
  font-size: 1em;
}
.applicationInfo .datatable tbody tr td:first-child {
  text-align: left;
  padding: 0.5em 0.5em 0.5em 1em;
}
.applicationInfo .datatable tbody tr td:first-child::after {
  content: ":";
}
.applicationInfo .datatable tbody tr td:last-child {
  text-align: right;
  padding: 0.5em 1em 0.5em 0.5em;
}
.applicationInfo.default .datatable {
  background-color: #f6f7fb;
  border-radius: 10px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.applicationInfo.shaded {
  box-shadow: 3px 3px 3px rgba(4, 4, 4, 0.2);
  padding: 1em;
  /* @include respond(960) {
    max-width: 23.25em;
    padding: 1.34em 1em;
  } */
}
.applicationInfo.shaded .title {
  text-align: center;
}
.applicationInfo.shaded .datatable {
  padding: 2.68em 1em 1em;
  font-weight: bold;
}
.applicationInfo.shaded .datatable thead {
  text-align: center;
}
.applicationInfo.shaded .datatable thead td {
  padding-bottom: 1.25em;
}
.applicationInfo.shaded .datatable tbody {
  font-size: 0.9em;
}
.applicationInfo.shaded .datatable tbody tr {
  border-bottom: 1px solid #e1e1e1;
}
.applicationInfo.shaded .datatable tbody tr td {
  padding: 0.8em 0.75em;
}
.applicationInfo.shaded .datatable tbody tr td:first-child {
  color: #707070;
  text-align: right;
}
.applicationInfo.shaded .datatable tbody tr:last-child {
  border-bottom: none;
}
.applicationInfo.shaded .link {
  color: #ed1b22;
}
.applicationInfo.centered .title {
  text-align: center;
}
.applicationInfo.centered .datatable {
  padding: 2.68em 1em 1em;
  font-weight: bold;
}
.applicationInfo.centered .datatable thead {
  text-align: center;
}
.applicationInfo.centered .datatable thead td {
  padding-bottom: 1.25em;
}
.applicationInfo.centered .datatable tbody {
  font-size: 0.9em;
}
.applicationInfo.centered .datatable tbody tr {
  border-bottom: 1px solid #e1e1e1;
}
.applicationInfo.centered .datatable tbody tr td {
  padding: 0.8em 0.75em;
}
.applicationInfo.centered .datatable tbody tr td:first-child {
  color: #707070;
  text-align: right;
}
.applicationInfo.centered .datatable tbody tr td:last-child {
  text-align: left;
}
.applicationInfo.centered .datatable tbody tr:last-child {
  border-bottom: none;
}